import React from 'react';
import { connect } from 'react-redux';
import {HashRouter as Router, Switch, Route, Link, Redirect} from 'react-router-dom';
import Info from './info';

class Dashboard extends React.Component {
    state = {
        name: '',
        avatar: '',
        hovered: false,
        recentPhotos: new Array,
    }
    setName = (name) => {
        this.setState({
            name: name
        })
    }
    setAvatar = (avatar) => {
        this.setState({
            avatar: avatar
        })
    }
    setPhotos = (photos) => {
        this.setState({
            photos: photos
        })
    }
    render() {
        let cookie = document.cookie.match(new RegExp('(^| )' + 'token' + '=([^;]+)'));
        const {name, avatar, hovered, recentPhotos} = this.state;  
        if (cookie) {
            return(
                <div className="dashFlex">
                    <div class="leftPanel">
                        {recentPhotos.length > 0 && (
                            recentPhotos.map((x,i) => {
                                return (
                                    <div key={i} className="widget">
                                    <img src={x.url} />
                                    </div>
                                )
                            })
                        )}
                    </div>
                    <div className="mainSection">
                        <div className="head">
                        <div class="headAvatar"><img src={avatar} /></div>
                        Welcome {name} </div>
                        <Info setPhotos={this.setPhotos} setName={this.setName} setAvatar={this.setAvatar} />
                    </div>
                    <div className="btnHolder">
                        <Link style={{'textDecoration': 'none'}} to ="/upload">
                            <div style={{'height': 'auto'}} 
                            onMouseEnter={() => this.setState({hovered: true})}
                            onMouseLeave={() => this.setState({hovered: false})}
                            id={!hovered ? "shadow1" : "shadow2"} className="btnDash">Schedule an upload</div>
                        </Link>
                        <Link style={{'textDecoration': 'none'}} to ="/scheduled">
                            <div style={{'height': 'auto'}} 
                            onMouseEnter={() => this.setState({hovered: true})}
                            onMouseLeave={() => this.setState({hovered: false})}
                            id={!hovered ? "shadow1" : "shadow2"} className="btnDash">View your scheduled posts</div>
                        </Link>
                        <div style={{'height': 'auto'}} 
                        onMouseEnter={() => this.setState({hovered: true})}
                        onMouseLeave={() => this.setState({hovered: false})}
                        id={!hovered ? "shadow1" : "shadow2"} className="btnDash">Logout of the associated instagram account</div>
                    </div>
                   
                    
                </div>
            )
        } else {
            return <div>ERROR</div>
        }
    }
}

export default connect((state) => ({

}))(Dashboard);